#intro{
    height: calc(100vh -4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;





}



.bg{
    position: absolute;
    top: 7rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;

    
  
}
.inroContent{
    color: white;
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;



}
.hello{
    font-size: 1.75rem;
    font-weight: 300;
}
.introName{

    color: yellow;

}
.intropara{

    font-size: medium;
    font-weight: 100;
    margin: 1rem 0;
    padding: 0.75rem;



}
.btn{
    background: white;
    border-radius: 30px;
}